@import "../../scss/variables";

.navbar{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    background-color:$cardNavBgColor ;
    a{
        color: white;
        font-size: 2rem;
        text-decoration: none;
        font-weight: bold;
        &:hover{
            background-color: $backgroundColor;
            color: $cardNavBgColor;
        }

    }
}
